.hero-title {
    position: absolute;
    top: 300px;
    font-size: 3em;
    font-family: playball;
    z-index: 20;
    width: 100%;

    color: white;
    text-align: center;

    text-shadow: rgb(3, 3, 3) 3px 3px 7px;
    pointer-events: none;
}

.hero-title h3 {
    position: relative;
    left: -100px;
    z-index: 20;
}

.hero-title h1  {
    position: relative;
    left: 100px;
    z-index: 20;
}

